body {
  margin: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: #1f1f1f;
  color: #e0e0e0;
}

a {
  color: #9ecfff;
  text-decoration: none;
}

a:hover {
  color: #ffffff;
}

header, footer {
  background-color: #2c2c2c;
  padding: 1rem;
  text-align: center;
}

nav ul {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: center;
  gap: 1.5rem;
}

nav li {
  font-size: 1.1rem;
}

main {
  padding: 2rem;
  max-width: 800px;
  margin: 0 auto;
}

footer {
  margin-top: 2rem;
  font-size: 0.9rem;
  border-top: 1px solid #444;
}

h1, h2, h3 {
  color: #ffffff;
}

button, .button {
  background-color: #444;
  color: #e0e0e0;
  padding: 0.5rem 1rem;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  margin-top: 0.5rem;
  transition: background-color 0.3s;
}

button:hover, .button:hover {
  background-color: #666;
}
